a {
    text-decoration: none;
    color: black;
}
a:hover {
    text-decoration: none;
    color: red;
}
img {
    width: 100%;
    max-height: 230px;
}
.toolBur {
    display: flex;
    justify-content: space-between;
}
.toolBur__iconBlock {
    display: flex;
    justify-content: flex-start;
    text-align: center;
}
.toolBur__cartWrapper {
    display: flex;
}
.toolBur__cartWrapper div {
    padding-left: 10px;
}
.product {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    text-align: center;
}
.product div {
    padding-bottom: 10px;
}
.itemShoppingCart {
    display: flex;
    justify-content: space-evenly;
}
.itemShoppingCart img {
    width: 100px;
    max-height: 100px;
}
.itemShoppingCart__buttons {
    display: flex;
    justify-content: space-between;
    width: 150px;
    align-items: center;
}
.itemShoppingCart__buttons button {
    height: 40px;
}
.itemShoppingCart__itemDescription {
    width: 250px;
}
.shoppingCart__shoppingListWrapper {
    display: flex;
    justify-content: space-between;
    text-align: center;
    flex-wrap: wrap;
}
.shoppingCart__shoppingList {
    width: 70%;
}
.shoppingCart__order {
    width: 400px;
}
.orderForm div {
    margin: 10px;
}

@media screen and (max-width: 584px) {
    .container {
        display: flex;
        justify-content: center;
    }
}
@media screen and (max-width: 1330px) {
    .shoppingCart__shoppingList {
        width: 100%;
    }
}
@media screen and (max-width: 800px) {
    .itemShoppingCart__itemDescription {
        display: none;
    }
}
